/* Base Styles */
.sponsors-page {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #fff;
  overflow-x: hidden;
  opacity: 0;
  transform: translateY(10px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.sponsors-page.mounted {
  opacity: 1;
  transform: translateY(0);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Hero Section */
.sponsors-hero {
  position: relative;
  height: 70vh;
  min-height: 400px;
  overflow: hidden;

  /* background image */
  background-image: url('sports-arena.jpg');
  background-size: cover;
  background-position: center;

  /* center text */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(14, 20, 32, 0.65);
  z-index: 1;
}

/* Headline and paragraph on bg */
.sponsors-hero h1,
.sponsors-hero p {
  position: relative;
  z-index: 2;
}

.sponsors-hero h1 {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 0.75rem;
  background: linear-gradient(90deg, #2de3b6, #4abfe6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 3px 12px rgba(0, 0, 0, 0.6);
  letter-spacing: 1px;
}

.sponsors-hero p {
  font-size: 1.25rem;
  font-weight: 400;
  max-width: 650px;
  margin: 0 auto;
  color: #eaf6ff;
  text-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
}

/* Intro Footnote */
.sponsors-intro {
  background-color: #0e1420;
  padding: 25px 0 35px;
  text-align: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  min-height: 120px;
  display: flex;
  align-items: center;
}

.sponsors-intro .container {
  width: 100%;
}

.sponsors-intro h2 {
  font-size: 1.7rem;
  margin-bottom: 12px;
  color: #2de3b6;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.sponsors-intro p {
  font-size: 1rem;
  line-height: 1.6;
  max-width: 720px;
  margin: 0 auto;
  color: #cfd8e3;
}

/* Sponsors Grid Section */
.sponsors-main {
  background: #0e1420;
  padding: 50px 0;
}

.sponsors-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 30px;
  margin-top: 20px;
}

.sponsor-card {
  background: #ffffff;
  border-radius: 14px;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.15);
  position: relative;
  z-index: 1;
}

.sponsor-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.25);
}

.sponsor-card a {
  display: block;
  padding: 25px 20px;
  text-decoration: none;
  color: inherit;
  height: 100%;
}

.card-inner {
  height: 110px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
  position: relative;
}

.sponsor-logo {
  max-width: 160px;
  max-height: 90px;
  width: auto;
  height: auto;
  object-fit: contain;
  transition: all 0.3s ease;
}

.sponsor-card:hover .sponsor-logo {
  transform: scale(1.08);
}

.logo-fallback {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: linear-gradient(45deg, #2de3b6, #4abfe6);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: bold;
  font-size: 22px;
}

.sponsor-name {
  display: block;
  text-align: center;
  font-weight: 600;
  color: #222;
  font-size: 15px;
  margin-top: 10px;
}

/* Responsive Design */
/* Sponsors Mobile Adjustments */
@media (max-width: 1400px){
  .sponsors-hero h1{
    font-size: 3rem;
  }
}

@media (max-width: 992px) {
  .sponsors-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .sponsors-hero {
    height: auto;
    min-height: 55vh;
  }

  .sponsors-hero h1 {
    font-size: 2.5rem;
  }

  .sponsors-hero p {
    font-size: 1.05rem;
  }

  .sponsors-intro {
    min-height: 140px;
  }
}

@media (max-width: 600px) {
  .sponsors-grid {
    grid-template-columns: 1fr;
    max-width: 100%;
    margin: 20px auto;
  }

  .sponsors-hero h1 {
    font-size: 2rem;
  }

  .sponsors-hero p {
    font-size: 1rem;
  }

  .sponsors-intro {
    min-height: 160px;
  }
}

@media (max-width: 450px) {
  .sponsors-hero h1 {
    font-size: 1.6rem;
  }

  .sponsors-hero p {
    font-size: 0.9rem;
  }
}
